.UserAuth{
    position: fixed;
    background-color: white;
    width: 40vw;
    box-shadow: 5px 5px black;
    top: 50%;
    left: 30vw;
    margin-left: auto;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    margin-right: auto;
    text-align: center;
    height: max-content;
    padding-bottom: 5vh;
    border-radius: 5px;
    z-index: 1;
}

#Auth-Title{
    width: 100%;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    text-decoration: underline black;
    box-shadow: 0px 5px rgb(219, 219, 219);
    padding-bottom: 5px;
    border-radius: 5px;
}

#Auth-Sub{
    text-align: left;
}

#sign-in-exit{
    float: right;
    border: none;
}