.Game-Tab{
    width: 30vw;
    height: 30vw;
    margin-left:2vw;
    background-size: contain;
    border: solid #ff5555 5px;
    background-color: #ff5555;
    background-size: cover;
    box-shadow: 5px 5px 5px black;
}

#Games-List{
    display: flex;
    margin-top:11vh;
}

#title{
    background-color: #ff5555;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    width: 75%;
    height: 20%;
    top: 30%;
    margin-top: 0px;
    font-size: 5vh;
    color:white;
    text-decoration: black underline;
    transition: width 0.1s, height 1s, top 1s;
    overflow: hidden;
}

#description{
    font-size: 2.5vh;
}


#title:hover {
    width: 100%;
    height: 100%;
    top: 0%;
    border: solid 1px #ff5555;
    margin-left: -1px;
}

#No-Game{
    margin-top: 11vh;
    margin-right: auto;
    margin-left:auto;
    text-align: center;
    color: white;
}